const axios = require('axios');

const Index = ({todos}) => {
  return (
    <div>
      {todos.map(todo => (
        <div>{todo.title}</div>
      ))}
    </div>
  )
}

Index.getInitialProps = async function () {
  const response = await axios.get('http://localhost:3009/todos');
console.log(response)
  return {
    todos: response.data,
  };
};

export default Index;
